.footer {
    background-color: @color-4;
    color: #ffffff;
    padding-top: 5rem;
    padding-bottom: 2rem;
    font-size: 1.6rem;
    .row {
        justify-content: space-between;
    }
    &-list {
        .col-auto();
        margin-bottom: 2rem;
        .col-lg(3);
        .col-md(6);
        .col-xs();
        li {
            margin-bottom: 1rem;
        }
    }
    &-title {
        font-size: 1.1em;
        font-weight: bold;
    }
    &-link {
        color: #ffffff;
        transition: opacity .3s;
        opacity: 0.7;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            left: 50%;
            right: 50%;
            bottom: -1em / 3;
            border-bottom: 1.5px solid #fff;
            transition: all .3s;
        }
        &:hover {
            opacity: 1;
            &::before {
                left: 5%;
                right: 5%;
            }
        }
        .icon {
            margin-right: 1em / 2;
        }
    }
    &-copyright {
        padding-top: 2rem;
        border-top: 1.5px solid fade(#ffffff, 25%);
        font-size: 0.8em;
    }
}