.main {
    padding-top: 8rem;
    padding-bottom: 8rem;
    background-color: lighten(@color-1, 12%);
    .media(@md, {
        padding-top: 6rem;
        padding-bottom: 6rem;
    });
}

.content {
    font-size: 1.8rem;
    line-height: 1.7;
    &:not(:last-child) {
        margin-bottom: 4em;
    }
    .media(@md, {
        font-size: 1.6rem;
        line-height: 1.5;
    }
    );
    .title();
    &-title {
        font-size: 1.7em;
    }
    &-subtitle {
        text-align: left;
        font-size: 1.2em;
        margin-bottom: 1em;
        font-weight: bold;
        &::after {
            content: '';
            display: block;
            width: 50px;
            border-bottom: 2px solid @color-text;
        }
        /*  */
        &-center {
            text-align: center;
            &::after {
                margin-right: auto;
                margin-left: auto;
            }
        }
        /*  */
        &-white {
            color: #ffffff;
            &::after {
                border-color: #ffffff;
            }
        }
    }
    &-text {
        .col(10);
        .col-lg();
        &:not(:last-child),
        &:first-child {
            margin-bottom: 1.5em;
        }
    }
    .container {
        background-color: #ffffff;
        position: relative;
        z-index: 10;
        .card(@color-1);
    }
    &-quote {
        .col();
        .card(@color-3);
        background-color: lighten(@color-3, 10%);
        color: #ffffff;
        overflow: hidden;
        text-align: center;
        position: relative;
        z-index: 10;
        font-size: .9em;
        &:not(:last-child) {
            margin-bottom: 1.5em;
        }
        small {
            display: block;
            margin-top: 1em;
            font-style: italic;
        }
        .icon-create(@fa-var-quote-left, {
            font-size: 7em;
            opacity: 0.05;
            position: absolute;
            top: 50%;
            margin-top: -1em / 2;
            left: 15px;
            z-index: -1;
        }
        );
    }
    &-card {
        .col();
        .card(@color-4);
        background-color: @color-4;
        color: #ffffff;
        text-align: center;
        margin-bottom: 1.5em;
    }
}

.level {
    &-body {
        .col(10);
        .col-lg();
        position: relative;
        margin-bottom: 1.5em;
    }
    &-range {
        height: 3px;
        align-self: flex-end;
        border-radius: 2px;
        background-image: linear-gradient(to right, #4CAF50, #FFEB3B, #d50000);
        margin-top: 5px;
        box-shadow: 1px 1px 2px fade(#b71c1c, 50%);
    }
    &-range-50 {
        max-width: 50%;
    }
    &-title {
        display: inline-block;
        font-size: .8em;
        opacity: 0.7;
    }
    .icon {
        margin-right: 10px;
        font-size: 2em;
        text-shadow: 1px 1px 1px fade(#000, 20%);
    }
    strong {
        display: inline-block;
        min-width: 8rem;
    }
}

.course {
    &-list {
        .col();
        margin-top: 4rem;
        margin-bottom: 4rem;
        li {
            &:not(:last-child) {
                margin-bottom: 1.5em;
            }
            /* icon */
            &:nth-of-type(1) .course-text {
                .icon-create(@fa-var-php);
            }
            &:nth-of-type(2) .course-text {
                .icon-create(@fa-var-database);
            }
            &:nth-of-type(3) .course-text {
                .icon-create('\e922', {
                    font-weight: 400;
                }
                );
            }
            &:nth-of-type(4) .course-text {
                .icon-create('\e91f', {
                    font-weight: 400;
                }
                );
            }
            &:nth-of-type(5) .course-text {
                .icon-create(@fa-var-laravel);
            }
            &:nth-of-type(6) .course-text {
                .icon-create(@fa-var-laravel);
            }
            /* icon end */
        }
    }
    &-link {
        display: block;
        position: relative;
        z-index: 100;
        text-align: center;
        background-color: @color-4;
        color: #ffffff;
        transition: 0.7s;
        .card(@color-1, 10px);
        &.active {
            border-bottom-right-radius: 0;
            border-bottom-left-radius: 0;
        }
        &::before {
            content: '';
            position: absolute;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: #ffffff;
            left: 15px;
            top: 50%;
            margin-top: -10px / 2;
        }
        &:hover {
            background-color: lighten(@color-4, 10%);
        }
    }
    &-text {
        .card(@color-4);
        height: 100%;
        border-top-right-radius: 0;
        border-top-left-radius: 0;
        z-index: 10;
        position: relative;
        text-align: center;
        overflow: hidden;
        &:not(.active) {
            display: none;
        }
        /* icon */
        &::before {
            font-size: 7em;
            opacity: 0.05;
            position: absolute;
            top: 50%;
            margin-top: -1em / 2;
            right: 15px;
            z-index: -1;
        }
        /* icon end */
    }
}

.process {
    &-image {
        .card(@color-1, 0, 0);
        position: relative;
        padding-bottom: floor(100% / 1.9);
        overflow: hidden;
        &:not(:first-child) {
            margin-top: 1.5em;
        }
        margin-bottom: 1.5em;
        img {
            position: absolute;
            top: -10%;
            left: 0;
            width: 100%;
        }
    }
}

.open {
    &-list {
        .col();
        list-style-type: disc;
        list-style-position: inside;
    }
    &-btn {
        padding-top: 1.5em;
        .media(@xs, {
            text-align: center;
        }
        );
    }
}

.group {
    .container {
        padding-top: 0;
        padding-bottom: 0;
    }
    a {
        color: inherit;
        position: relative;
        &::before {
            content: '';
            position: absolute;
            left: 50%;
            right: 50%;
            bottom: -1em / 3;
            border-bottom: 1.5px solid #fff;
            transition: all .3s;
        }
        &:hover {
            opacity: 1;
            &::before {
                left: 5%;
                right: 5%;
            }
        }
    }
    .row {
        justify-content: center;
    }
    &-tooltip {
        &:hover {
            &::before {
                z-index: 100;
                opacity: 1;
                top: -6em;
            }
        }
        &::before {
            content: attr(data-set-tooltip);
            font-size: 0.7em;
            position: absolute;
            z-index: -1;
            top: 0;
            right: 0;
            left: 50%;
            margin-left: -7em / 2;
            background-color: #ffffff;
            width: 7em;
            color: fade(@color-text, 75%);
            .card(@color-4, .5em, .5em);
            opacity: 0;
            transition: all .5s;
        }
    }
    &-table {
        .col(3.5);
        .col-lg(4);
        .col-md(6);
        .col-sm(6);
        .col-xs();
        .card(@color-1, 0, 0);
        text-align: center;
        margin-top: 4rem;
        margin-bottom: 4rem;
        transition: box-shadow .5s;
        position: relative;
        &:hover {
            .bxsh(@color-4);
        }
    }
    &-header {
        height: 5em;
        background-color: @color-4;
        position: relative;
        border-top-right-radius: 5px;
        border-top-left-radius: 5px;
    }
    &-title {
        font-size: 2.5em;
        width: 2.5em;
        height: 2.5em;
        position: absolute;
        left: 50%;
        margin-left: -2.5em / 2;
        top: -.7em;
        .card(@color-4, 0, 0);
        padding-top: 0.3em;
        border-radius: 50%;
        background-color: #ffffff;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        line-height: 1;
        text-shadow: 1px 1px 1px fade(#000000, 20%);
        span {
            font-size: .3em;
            opacity: 0.5;
        }
    }
    &-body {
        padding-top: 3rem;
        padding-bottom: 3rem;
        font-size: 0.8em;
        color: fade(@color-text, 70%);
    }
    &-text {
        &:not(:first-of-type) {
            padding-top: 1rem;
        }
        &:not(:last-of-type) {
            border-bottom: 1.5px solid fade(#000000, 15%);
            padding-bottom: 1rem;
        }
    }
    &-footer {
        background-color: @color-4;
        color: #ffffff;
        padding-top: 1rem;
        padding-bottom: 1rem;
        border-bottom-right-radius: 5px;
        border-bottom-left-radius: 5px;
        font-size: 0.9em;
    }
    &-footer &-text {
        border-color: fade(#ffffff, 25%);
    }
}

